<template>
	<div class="logout-box">
		<div class="user">
			<div class="acator">
				<img v-if="user.imgUrl" :src="user.imgUrl">
				<img else src="../../assets/images/user.png">
			</div>
			<div class="name" v-text="user.userName" :title="user.userName"></div>
		</div>
		<ul class="logoutList">
			<LI v-for="(btn,index) in buttonList" v-text="btn.btnTxt" @click="handleClick(btn,index)" class="button-item"></LI>
		</ul>
	</div>
</template>

<script>
	import { mapMutations, mapGetters } from 'vuex';
	export default {
		props: {
			user: {
				type: Object,
				required: true
			},
			buttonList: {
				type: Array,
				required: true
			}
		},
		methods: {
			handleClick(btnItem, index){
				if(++index == this.buttonList.length){
					this.SET_LOGIN_OUT()
					this.$router.push('/37duc/user/smsLogin')
					return
				}
				this.$router.push(btnItem.route)
			},
			...mapMutations(['SET_LOGIN_OUT'])
		},
		data() {
			return {
				
			};
		}
	}
</script>

<style lang="scss" scoped>
	@import '@/assets/styles/mixin.scss';
	.logout-box{
		.user{
			padding: 15px 8px;
			border-bottom: 1px solid #F1F4F4;
			.acator{
				padding: 0 5px;
				width: 40px;
				height: 40px;
				box-sizing: content-box;
				font-size: 0;
				border-radius: 50%;
				overflow: hidden;
				float: left;
				img{
					display: block;
					height: 100%;
				}
			}
			.name{
				padding: 0 5px;
				line-height: 40px;
				color: #B6BBBB;
				font-size: 16PX;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				text-align: left;
			}
		}
		.logoutList{
			.button-item{
				padding: 16px 0px;
				text-align: center;
				font-size: 16px;
				color: $color-text;
				line-height: 16px;
				cursor: pointer;
			}
			&:hover{
				background-color: #F1F4F4;
			}
		}
	}
</style>
